@import url(https://fonts.googleapis.com/css2?family=Bad+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  background-image: url(data:image/png;base64,);
  background-repeat: repeat;
}

.topicInput {
  margin: 4px;
}

h1 {
  font-family: 'Bad Script', cursive;
}

.attribution {
  font-size: 6px;
  color: #c7a4a4;
}

.link-button {
  background-color: transparent;
  border: none;
  display: inline;
  margin: 0;
  padding: 0;
}

.link-button:hover,
.link-button:focus {
text-decoration: none;
}

.iphoneHint {
  margin: 10px;
  font-style: italic;
  font-size: 9px;
}

.attribution a {
  color: #c7a4a4;
}

.iconsAttribution {
  float: left;
  width: 50%;
}

.musicAttribution {
  width: 50%;
  float: right;
}

.playingIcon {
  margin-top: 50px;
  width: 90%;
  height: 58%;
}

.controls {
  margin-top: -15px;
  margin-bottom: 30px;
}

.songTitle {
  margin-top: 30px;
}

.control-buttons {
  margin: 3px;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
}

header {
  position: absolute;
  top: 0;
  width: 100%;
}

.navButtons {
  margin: 20px;
}

.song-container {
  text-align: center;
}

.loadingAnimation {
  width: 100%;
}

/* Dirty hack to stop the text in the alert being uppercased. */
#__react-alert__ div {
  text-transform: none!important;
  text-transform: initial!important;
}

.loader-hidden {
  display: none;
}

.loader-active {
  transform: scale(3, 3);
  display: inline-block;
} 

